import React from 'react';


const Footer = () => {
  return (
    <footer className="bg-gray-900 text-white py-12">
      <div className="container mx-auto px-4">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {/* School Info */}
          <div>
            <h3 className="text-xl font-bold mb-4">Sun Rise International</h3>
            <p className="text-gray-400 mb-2">Empowering minds, Building futures</p>
            <address className="text-gray-400 not-italic">
              123 Education Street<br />
              Knowledge City, ST 12345<br />
              Phone: (555) 123-4567<br />
              Email: info@sunriseschool.edu
            </address>
          </div>

          {/* Quick Links */}
          <div>
            <h3 className="text-xl font-bold mb-4">Quick Links</h3>
            <div className="space-y-2 flex justify-between">
              <a to="/about" className="text-gray-400 hover:text-white transition-colors">About Us</a>
              <a to="/academics" className="text-gray-400 hover:text-white transition-colors">Academics</a>
              <a  to="/admissions" className="text-gray-400 hover:text-white transition-colors">Admissions</a>
              <a  to="/events" className="text-gray-400 hover:text-white transition-colors">Events</a>
              <a to="/contact" className="text-gray-400 hover:text-white transition-colors">Contact</a>
            </div>
          </div>

          {/* Connect With Us */}
          <div>
            <h3 className="text-xl font-bold mb-4">Connect With Us</h3>
            <div className="flex space-x-4">
              <a href="#" className="text-gray-400 hover:text-white transition-colors">
                <i className="fab fa-facebook-f"></i>
              </a>
              <a href="#" className="text-gray-400 hover:text-white transition-colors">
                <i className="fab fa-twitter"></i>
              </a>
              <a href="#" className="text-gray-400 hover:text-white transition-colors">
                <i className="fab fa-instagram"></i>
              </a>
              <a href="#" className="text-gray-400 hover:text-white transition-colors">
                <i className="fab fa-linkedin-in"></i>
              </a>
            </div>
          </div>

          {/* Newsletter */}
          <div>
            <h3 className="text-xl font-bold mb-4">Newsletter</h3>
            <p className="text-gray-400 mb-4">Subscribe to stay updated with school news and events.</p>
            <form className="space-y-2">
              <input
                type="email"
                placeholder="Enter your email"
                className="w-full px-4 py-2 bg-gray-800 text-white rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
              <button
                type="submit"
                className="w-full px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors"
              >
                Subscribe
              </button>
            </form>
          </div>
        </div>

        {/* Bottom Bar */}
        <div className="border-t border-gray-800 mt-8 pt-8 text-center text-gray-400">
          <p>&copy; {new Date().getFullYear()} Sun Rise International. All rights reserved.</p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;