import React from 'react';

const About = () => {
  return (
    <div className="pt-20 px-4 md:px-8 lg:px-16 max-w-7xl mx-auto">
      <h1 className="text-4xl font-bold text-red-700  text-center mb-12">About Our School</h1>
      
      <section className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Our History</h2>
        <p className="text-gray-700 mb-6">
          Founded in 1995, our school has been a beacon of academic excellence for over two decades.
          What started as a small institution with just 50 students has grown into a premier educational
          establishment serving over 1,000 students from diverse backgrounds.
        </p>
      </section>

      <section className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Our Vision</h2>
        <p className="text-gray-700 mb-6">
          To be a leading educational institution that nurtures creative thinkers, innovative problem solvers,
          and responsible global citizens who will shape the future of our world.
        </p>
      </section>

      <section className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Our Mission</h2>
        <p className="text-gray-700 mb-6  ">
          We are committed to providing a holistic education that empowers students to excel academically,
          develop personally, and contribute meaningfully to society through:
        </p>
        <ul className="list-disc pl-6 text-gray-700">
          <li className="mb-2">Excellence in teaching and learning</li>
          <li className="mb-2">Character development and values education</li>
          <li className="mb-2">Innovation and technological advancement</li>
          <li className="mb-2">Community engagement and social responsibility</li>
        </ul>
      </section>

      <section className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Core Values</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          <div className="p-6 bg-white rounded-lg shadow-md">
            <h3 className="text-xl font-semibold mb-3">Excellence</h3>
            <p className="text-gray-700">Striving for the highest standards in everything we do</p>
          </div>
          <div className="p-6 bg-white rounded-lg shadow-md">
            <h3 className="text-xl font-semibold mb-3">Integrity</h3>
            <p className="text-gray-700">Maintaining honesty and strong moral principles</p>
          </div>
          <div className="p-6 bg-white rounded-lg shadow-md">
            <h3 className="text-xl font-semibold mb-3">Innovation</h3>
            <p className="text-gray-700">Embracing new ideas and creative solutions</p>
          </div>
        </div>
      </section>
    </div>
  );
};

export default About;