import React, { useState } from 'react';
import { Form, Input, Button, message } from 'antd';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const AddClass = () => {
  const [form] = Form.useForm();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  const onFinish = async (values) => {
    try {
      setLoading(true);
      await axios.post('http://localhost:5000/api/classes', values, {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });
      message.success('Class created successfully');
      navigate('/admin/classes');
    } catch (error) {
      message.error(error.response?.data?.message || 'Error creating class');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-6">
      <h2 className="text-2xl font-semibold mb-6">Add New Class</h2>
      <Form
        form={form}
        layout="vertical"
        onFinish={onFinish}
        className="max-w-md"
      >
        <Form.Item
          name="name"
          label="Class Name"
          rules={[{ required: true, message: 'Please input class name!' }]}
        >
          <Input placeholder="e.g., Class 1A" />
        </Form.Item>

        <Form.Item
          name="section"
          label="Section"
          rules={[{ required: true, message: 'Please input section!' }]}
        >
          <Input placeholder="e.g., A" />
        </Form.Item>

        <Form.Item
          name="capacity"
          label="Capacity"
          rules={[{ required: true, message: 'Please input capacity!' }]}
        >
          <Input type="number" placeholder="e.g., 30" />
        </Form.Item>

        <Form.Item>
          <Button type="primary" htmlType="submit" loading={loading}>
            Create Class
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default AddClass;