import React, { useState } from 'react';

import { motion } from 'framer-motion';
import { useRouter } from 'next/router';
const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const router=useRouter();
  return (
    <nav className="fixed w-full z-50 bg-white/95 shadow-sm">
      <div className="container mx-auto px-4 sm:px-6 lg:px-8 py-4">
        <div className="flex justify-between items-center">
        <a  to="/" className="text-2xl font-bold text-blue-900 tracking-tight">
            Sun Rise
            </a>
          <div className="hidden md:flex items-center space-x-4 lg:space-x-6">
            <a onClick={()=>{
                router.push("/LandingPage/Home");
            }} className="text-gray-600 hover:text-blue-600 transition-colors duration-300 whitespace-nowrap">
              Home
              </a>
              <a onClick={()=>{
                router.push("/LandingPage/About")
              }}    className="text-gray-600 hover:text-blue-600 transition-colors duration-300 whitespace-nowrap">
              About
              </a>
              <a onClick={()=>{
                router.push("/LandingPage/Academics")
              }} to="/academics" className="text-gray-600 hover:text-blue-600 transition-colors duration-300 whitespace-nowrap">
              Academics
              </a>
              <a onClick={()=>{
                router.push("/LandingPage/Admissions")
              }} to="/admissions" className="text-gray-600 hover:text-blue-600 transition-colors duration-300 whitespace-nowrap">
              Admissions
              </a>
              <a onClick={()=>{
                router.push("/LandingPage/Contact")
              }} className="text-gray-600 hover:text-blue-600 transition-colors duration-300 whitespace-nowrap">
              Contact
              </a>
              <a onClick={()=>{
                router.push("/Login")
              }} className="text-gray-600 hover:text-blue-600 transition-colors duration-300 whitespace-nowrap">
              Login
              </a>
          </div>
          <motion.button
            onClick={() => setIsOpen(!isOpen)}
            className="md:hidden text-gray-600"
            whileTap={{ scale: 0.95 }}
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
            </svg>
          </motion.button>
        </div>
      </div>

      {/* Mobile menu */}
      <div
        className={`${isOpen ? 'translate-y-0 opacity-100' : '-translate-y-full opacity-0'} md:hidden fixed top-[4rem] left-0 w-full bg-white py-4 shadow-lg transition-all duration-300 ease-in-out transform`}
      >
        <div className="container mx-auto px-8 space-y-4">
        <a to="/" className="block text-gray-600 hover:text-blue-600 transition-colors duration-300">Home</a>
        <a  to="/about" className="block text-gray-600 hover:text-blue-600 transition-colors duration-300">About</a>
        <a  to="/academics" className="block text-gray-600 hover:text-blue-600 transition-colors duration-300">Academics</a>
        <a  to="/admissions" className="block text-gray-600 hover:text-blue-600 transition-colors duration-300">Admissions</a>
        <a  to="/contact" className="block text-gray-600 hover:text-blue-600 transition-colors duration-300">Contact</a>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;