import React from 'react';

const Admissions = () => {
  return (
    <div className="pt-20 px-4 md:px-8 lg:px-16 max-w-7xl mx-auto bg-gradient-to-b from-blue-50 to-white">
      <h1 className="text-4xl font-bold text-center mb-12">Admissions</h1>

      <section className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Admission Process</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="p-6 bg-white rounded-lg shadow-md">
            <h3 className="text-xl font-semibold mb-3">Step 1: Application</h3>
            <p className="text-gray-700">Complete and submit the online application form with required documents</p>
          </div>
          <div className="p-6 bg-white rounded-lg shadow-md">
            <h3 className="text-xl font-semibold mb-3">Step 2: Assessment</h3>
            <p className="text-gray-700">Schedule and complete the entrance assessment</p>
          </div>
          <div className="p-6 bg-white rounded-lg shadow-md">
            <h3 className="text-xl font-semibold mb-3">Step 3: Interview</h3>
            <p className="text-gray-700">Parent and student interview with school administrators</p>
          </div>
        </div>
      </section>

      <section className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Requirements</h2>
        <div className="p-6 bg-white rounded-lg shadow-md mb-6">
          <h3 className="text-xl font-semibold mb-3">Required Documents</h3>
          <ul className="list-disc pl-6 text-gray-700">
            <li className="mb-2">Completed application form</li>
            <li className="mb-2">Birth certificate</li>
            <li className="mb-2">Previous academic records</li>
            <li className="mb-2">Passport-size photographs</li>
            <li className="mb-2">Transfer certificate (if applicable)</li>
          </ul>
        </div>
      </section>

      <section className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Fee Structure</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="p-6 bg-white rounded-lg shadow-md">
            <h3 className="text-xl font-semibold mb-3">Primary Section</h3>
            <ul className="text-gray-700">
              <li className="mb-2">Application Fee: $50</li>
              <li className="mb-2">Registration Fee: $500</li>
              <li className="mb-2">Annual Tuition: $8,000</li>
              <li className="mb-2">Activity Fee: $200</li>
            </ul>
          </div>
          <div className="p-6 bg-white rounded-lg shadow-md">
            <h3 className="text-xl font-semibold mb-3">Secondary Section</h3>
            <ul className="text-gray-700">
              <li className="mb-2">Application Fee: $50</li>
              <li className="mb-2">Registration Fee: $500</li>
              <li className="mb-2">Annual Tuition: $10,000</li>
              <li className="mb-2">Activity Fee: $300</li>
            </ul>
          </div>
        </div>
      </section>

      <section className="mb-12">
        <h2 className="text-2xl font-semibold mb-4">Online Application Form</h2>
        <form className="p-6 bg-white rounded-lg shadow-md">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="block text-gray-700 mb-2" htmlFor="studentName">
                Student's Full Name
              </label>
              <input
                type="text"
                id="studentName"
                className="w-full p-2 border rounded-md"
                required
              />
            </div>
            <div>
              <label className="block text-gray-700 mb-2" htmlFor="dob">
                Date of Birth
              </label>
              <input
                type="date"
                id="dob"
                className="w-full p-2 border rounded-md"
                required
              />
            </div>
            <div>
              <label className="block text-gray-700 mb-2" htmlFor="grade">
                Applying for Grade
              </label>
              <select
                id="grade"
                className="w-full p-2 border rounded-md"
                required
              >
                <option value="">Select Grade</option>
                <option value="1">Grade 1</option>
                <option value="2">Grade 2</option>
                <option value="3">Grade 3</option>
                <option value="4">Grade 4</option>
                <option value="5">Grade 5</option>
                <option value="6">Grade 6</option>
                <option value="7">Grade 7</option>
                <option value="8">Grade 8</option>
                <option value="9">Grade 9</option>
                <option value="10">Grade 10</option>
                <option value="11">Grade 11</option>
                <option value="12">Grade 12</option>
              </select>
            </div>
            <div>
              <label className="block text-gray-700 mb-2" htmlFor="email">
                Parent's Email
              </label>
              <input
                type="email"
                id="email"
                className="w-full p-2 border rounded-md"
                required
              />
            </div>
          </div>
          <div className="mt-6">
            <button
              type="submit"
              className="bg-blue-600 text-white px-6 py-2 rounded-md hover:bg-blue-700"
            >
              Submit Application
            </button>
          </div>
        </form>
      </section>
    </div>
  );
};

export default Admissions;